
html{-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%
}
body{line-height:1.6;font-family:-apple-system-font,"Helvetica Neue",sans-serif
}
*{margin:0;padding:0
}
a img{border:0
}
a{text-decoration:none;-webkit-tap-highlight-color:rgba(0,0,0,0)
}
::-webkit-input-placeholder{font-family:-apple-system-font,"Helvetica Neue",sans-serif
}
a{-webkit-touch-callout:none
}
html,body{font-family:PingFangSC-Medium;width:100%;height:100%;overflow-x:hidden;-webkit-overflow-scrolling:touch;background-color:#ffffff
}
#app{overflow-y:auto;z-index:999;-webkit-overflow-scrolling:touch
}
.vux-table td:before,.vux-table th:before{border-top:0 !important;border-bottom:0 !important;border-left:0 !important;border-right:0 !important
}
.vux-table:after{border-top:0 !important
}
.vux-table td,.vux-table th{border-top:0 !important;border-bottom:0 !important;border-left:0 !important;border-right:0 !important
}
.vux-table td:after,.vux-table th:after{border-top:0 !important;border-bottom:0 !important;border-left:0 !important;border-right:0 !important
}
.weui-tab__panel{padding-top:50px
}


